:root {
  --header-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__logo-link {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.header__nav {
  display: block;
}

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__list-item:not(:first-child) {
  margin-left: 20px;
}

.header__nav-link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active {
  color: var(--header-color);
}

.is-active:after {
  content: "";
  background-color: var(--header-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__list-item a:hover {
  color: var(--header-color);
}

